"use client";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination, Autoplay } from "swiper/modules";
import styles from "@/app/styles/componentStyles/";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";

const images = [
  "/images/carousel/constuct.png",
  "/images/carousel/influencer.png",
  "/images/carousel/magway.png",
  "/images/carousel/vrach.png",
  "/images/carousel/webovio.png",
];

export default function WorksCarousel() {
  return (
    <div className={styles.swiper}>
      <Swiper
        modules={[Navigation, Pagination, Autoplay]}
        spaceBetween={50} // Отступ между слайдами
        slidesPerView={1} // Количество отображаемых слайдов
        loop={true} // Бесконечный цикл
        navigation // Включаем кнопки "вперёд/назад"
        pagination={{ clickable: true }} // Включаем пагинацию (точки)
        autoplay={{ delay: 3000 }} // Автопрокрутка (3000 мс = 3 сек)
        className="rounded-2xl overflow-hidden"
      >
        {images.map((src, index) => (
          <SwiperSlide key={index}>
            <img src={src} alt={`Slide ${index + 1}`} className="w-40 h-30 object-cover mx-auto" />
          </SwiperSlide>
        ))}
      </Swiper>
    </div>
  );
}
